
import { defineComponent } from 'vue';
import api from "../api/backend-api";
import {AxiosError} from "axios";

interface State {
  user: {
    id: number
    firstName: string,
    lastName: string;
  };
  retrievedUser: {
    id: number
    firstName: string,
    lastName: string;
  };
  showResponse: boolean;
  showRetrievedUser: boolean;
  errors: AxiosError[]
}

export default defineComponent({
  name: 'User',

  data: (): State => {
    return {
      errors: [],
      user: {
        id: 0,
        firstName: '',
        lastName: ''
      },
      showResponse: false,
      retrievedUser: {
        id: 0,
        firstName: '',
        lastName: ''
      },
      showRetrievedUser: false
    }
  },
  methods: {
    // Fetches posts when the view is created.
    createNewUser () {
      api.createUser(this.user.firstName, this.user.lastName).then(response => {
          // JSON responses are automatically parsed.
          this.user.id = response.data;
          console.log('Created new User with Id ' + response.data);
          this.showResponse = true
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    retrieveUser () {
      api.getUser(this.user.id).then(response => {
          // JSON responses are automatically parsed.
          this.retrievedUser = response.data;
          this.showRetrievedUser = true
        })
        .catch((error: AxiosError):void => {
          this.errors.push(error)
        })
    }
  }
});
