<template>
  <div class="home">
    <h1>{{ msg }}</h1>
    <h5>REST service call are easy to do with Vue.js, if you know how to do it.</h5>
    <p></p>
    <h6><span class="badge bg-primary"> Let´s go!</span> Call a Spring Boot REST backend service, by clicking a button:
    </h6>
    <p></p>
    <button class="btn btn-success" @click="getServerInfo()">/hello (GET server data)</button>

    <h6>API id: <span class="badge bg-primary">{{ serverId }}</span></h6>
    <h6>Valid until: <span class="badge bg-primary">{{ validUntil }}</span></h6>
    <h6>My server time: <span class="badge bg-primary">{{ serverTime }}</span></h6>
    <h6>Cosmos API server time: <span class="badge bg-primary">{{ updateTime }}</span></h6>
    <h6>Next update: <span class="badge bg-primary">{{ nextUpdate }}</span></h6>

  </div>
</template>

<script lang="ts">
import {defineComponent} from 'vue';
import api from '../api/backend-api'
import {AxiosError, AxiosRequestConfig} from "axios";
import {min} from "@popperjs/core/lib/utils/math";

interface State {
  showResponse: boolean;
  serverId: string;
  validUntil: string;
  serverTime: string;
  updateTime: string;
  nextUpdate: bigint;
  nextUpdateInWords: string;
  errors: AxiosError[];
}

export default defineComponent({
  name: 'Home',

  data: (): State => {

    return {
      showResponse: false,
      serverId: '',
      validUntil: '',
      serverTime: '',
      updateTime: '',
      nextUpdate: 1n,
      errors: [],
      nextUpdateInWords: ''
    }
  },
  methods: {
    async getServerInfo() {
      api.getServerInfo().then(response => {
        this.serverId = response.data.id;
        this.validUntil = response.data.validUntil;
        this.serverTime = response.data.serverTime;
        this.updateTime = response.data.updateTime;
        this.nextUpdate = response.data.nextUpdate;

      })
          .catch((error: AxiosError) => {
            this.errors.push(error)
          })
    },

  }

})


</script>
