
import {defineComponent} from 'vue';
import api from '../api/backend-api'
import {AxiosError, AxiosRequestConfig} from "axios";
import {min} from "@popperjs/core/lib/utils/math";

interface State {
  showResponse: boolean;
  serverId: string;
  validUntil: string;
  serverTime: string;
  updateTime: string;
  nextUpdate: bigint;
  nextUpdateInWords: string;
  errors: AxiosError[];
}

export default defineComponent({
  name: 'Home',

  data: (): State => {

    return {
      showResponse: false,
      serverId: '',
      validUntil: '',
      serverTime: '',
      updateTime: '',
      nextUpdate: 1n,
      errors: [],
      nextUpdateInWords: ''
    }
  },
  methods: {
    async getServerInfo() {
      api.getServerInfo().then(response => {
        this.serverId = response.data.id;
        this.validUntil = response.data.validUntil;
        this.serverTime = response.data.serverTime;
        this.updateTime = response.data.updateTime;
        this.nextUpdate = response.data.nextUpdate;

      })
          .catch((error: AxiosError) => {
            this.errors.push(error)
          })
    },

  }

})


